
.detail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  font-family: "Microsoft YaHei", "微软雅黑", "MicrosoftJhengHei";
  -webkit-transition: all 0.6s;
  transition: all 0.6s;
}
.detail p {
  text-indent: 28px;
  font-size: 14px;
  color: #555;
  line-height: 25px;
}
.detail.detailMove-transition {
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
}
.detail.detailMove-enter,
.detail.detailMove-leave-active {
  -webkit-transform: translate3D(100%, 0, 0);
          transform: translate3D(100%, 0, 0);
}
.detail h1 {
  margin-top: 20px;
  font-size: 16px;
  color: #000;
  line-height: 16px;
  padding-left: 10px;
  margin: 0.67em 0;
  font-weight: 600;
}
.detail .date {
  margin-bottom: 10px;
  font-size: 13px;
  color: #a9aeb1;
  line-height: 13px;
  padding-left: 10px;
  font-weight: 600;
}
.detail .pubPerson {
  font-size: 14px;
  color: #808080;
  padding-left: 10px;
  font-weight: 600;
}
.detail .artlinel,
.detail .artliner {
  width: 60%;
  border-top: 1px solid #e3e3e3;
  position: relative;
  margin-top: 15px;
  margin-bottom: 15px;
}
.detail .artliner {
  float: right;
}
.detail .artlinel img,
.detail .artliner img {
  width: 30px;
  position: absolute;
  left: 100%;
  top: -15px;
}
.detail .artliner img {
  left: 0;
  right: 100%;
}
.detail .subhead {
  font-size: 14px;
  color: #956722;
  line-height: 22px;
  padding: 10px;
}
.detail .art-cont {
  padding: 10px;
}
.detail .art-cont h3 {
  padding: 2px;
  font-weight: bold;
  border: 2px solid #ccc;
}
.detail .art-cont h4 {
  color: #337fe5;
  font-size: 14px;
  font-weight: bold;
  margin: 10px 0 4px 0;
}
.detail .art-cont .h4 {
  text-indent: 25px;
}
.detail .art-cont .img1 {
  width: 100%;
  display: block;
  margin: 10px auto;
}
.detail .art-cont .summary {
  margin-top: 30px;
}
.detail .from {
  text-align: center;
  font-size: 14px;
  color: #555;
  line-height: 25px;
  margin-top: 100px;
}
.detail .QRcode {
  width: 140px;
  display: block;
  margin: 0 auto;
  margin-top: 60px;
}
.detail .zan {
  display: block;
  width: 80px;
  margin: 0 auto;
  margin-top: 60px;
}


