
.tab {
  width: 100%;
  position: fixed;
  top: 50px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  font-size: 0;
  background: #fff;
  border-bottom: 10px solid #fafafa;
  overflow: hidden;
}
.tab li {
  font-size: 16px;
  display: inline-block;
  margin-left: 9px;
  margin-right: 9px;
}


