
.head {
  height: 50px;
}
.head-wrapper {
  position: fixed;
  top: 0;
  width: 100%;
  height: 50px;
  background: #94b214;
  padding: 0 6px;
  text-align: center;
  z-index: 90;
  display: flex;
  display: -ms-flexbox;
  display: -webkit-box;
  display: -webkit-flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-align: space-between;
      align-items: space-between;
  -webkit-box-align: space-between;
  -webkit-box-pack: space-between;
  -webkit-justify-content: space-between;
}
.head-left {
  margin-top: 15px;
  margin-right: 15px;
}
.head-left span {
  display: inline-block;
  font-size: 0;
}
.head-left span img {
  height: 20px;
}
.back-img {
  height: 20px;
  margin-top: 15px;
}
.logo {
  width: 115px;
  margin-top: 6px;
}
.nav-lf {
  width: 40%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  text-align: left;
  overflow: hidden;
  -webkit-transition: all 0.6s;
  transition: all 0.6s;
}
.nav-lf.nav-left-move-transition {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.nav-lf.nav-left-move-enter,
.nav-lf.nav-left-move-leave-active {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.nav-lf .nav-lf-btn {
  background: #ff8300;
  width: 100%;
  height: 50px;
  overflow: hidden;
}
.nav-lf .nav-lf-btn .nav-lf-btn2 {
  display: inline-block;
  height: 100%;
}
.nav-lf .nav-lf-btn .nav-lf-btn2 img {
  width: 25px;
  margin-left: 10px;
  margin-top: 12px;
  margin-bottom: 7px;
}
.nav-lf .nav-lf-ct {
  background-color: #94b214;
  font-size: 14px;
  height: 100%;
  padding: 23px 20px 20px 20px;
}
.nav-lf .nav-lf-ct a {
  color: #fff;
  text-decoration: none;
  outline: none;
  font-family: "Microsoft YaHei";
  margin-left: 10%;
}
.search {
  width: 100%;
  position: fixed;
  top: 50px;
  left: 0;
  z-index: 1;
  -webkit-transition: all 0.8s;
  transition: all 0.8s;
  background: #fff;
  font-family: "Microsoft YaHei";
}
.search.search-move-transition {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  opacity: 1;
}
.search.search-move-enter,
.search.search-move-leave-active {
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
  opacity: 0;
}
.search .search-body {
  position: relative;
  height: 70px;
  width: 100%;
  margin-left: 10px;
}
.search .search-body .search-inpt {
  width: 90%;
  border: 1px solid #c9c9c9;
  height: 40px;
  margin: 15px auto;
  padding-left: 10px;
  border-radius: 6px;
  outline: none;
  color: #c9c9c9;
  font-weight: 400;
}
.search .search-body .search-submit {
  background: rgba(0,0,0,0);
  border: none;
  position: absolute;
  right: 25px;
  top: 20px;
}
.search .search-body .search-submit .search-submit-img {
  width: 26px;
  height: 26px;
}
.search .search-hot-wrapper {
  padding: 10px;
  border-bottom: 1px solid #c9c9c9;
  border-top: 1px solid #c9c9c9;
  font-size: 0;
}
.search .search-hot-wrapper .search-hot {
  display: inline-block;
  font-weight: normal;
  font-size: 16px;
  margin: 10px 2.1%;
  border: 1px solid #ccc;
  padding: 5px 12px 4px 12px;
  border-radius: 20px;
  color: #666;
}
.channel {
  width: 100%;
  position: fixed;
  top: 50px;
  z-index: 1;
  background: #fff;
  -webkit-transition: all 0.8s;
  transition: all 0.8s;
  font-family: "Microsoft YaHei";
  overflow: hidden;
}
.channel.channel-move {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  opacity: 1;
}
.channel.channel-move-enter,
.channel.channel-move-leave-active {
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
  opacity: 0;
}
.channel a {
  display: inline-block;
  font-size: 16px;
  border: 1px solid #c9c9c9;
  border-radius: 15px;
  width: 20%;
  margin: 10px 2.1%;
  text-align: center;
  padding-top: 4px;
  padding-bottom: 4px;
  color: #666;
  text-decoration: none;
  outline: none;
  background-color: #fff;
}
.channel h2 {
  font-size: 17px;
  color: #1a1a1a;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #e3e3e3;
  padding-left: 10px;
}


